import "./styles.css";
import { useState } from "react";
import data from "./images.json";

function Gallery(props) {
  const [image, setImage] = useState(0);
  const [favourites, setFavourites] = useState([]);
  const urls = data.map((data) => data.urls.regular);
  const name = data.map((data) => data.alt_description);

  const prev = () => {
    setImage(image - 1);
  };
  const next = () => {
    setImage(image + 1);
  };

  const fav = (e) => {
    console.log(name[image]);
    if (favourites.includes(name[image])) {
      setFavourites(favourites.filter((fav) => fav !== name[image]));
    }
    if (!favourites.includes(name[image])) {
      setFavourites([...favourites, name[image]]);
    }
  };
  const deleteFav = (e) => {
    setFavourites(favourites.filter((fav) => fav !== e));
  };

  return (
    <div className="card">
      <h1>{name[image]}</h1>
      <img src={urls[image]} alt="" />
      <button className="btn" onClick={prev}>
        Previous
      </button>
      <button className="btn" onClick={fav}>
        Favourite
      </button>
      <button className="btn" onClick={next}>
        Next
      </button>

      <h5>Favourites</h5>
      {favourites.map((favourite) => (
        <>
          <p key={favourite}>
            {favourite}
            <button onClick={() => deleteFav(favourite)}>x</button>
          </p>
        </>
      ))}
    </div>
  );
}
export default function App() {
  return (
    <view>
      <table>
        <div>
          <p align="center">
            <h1>Gallery</h1>
          </p>
          <Gallery />
        </div>
      </table>
    </view>
  );
}
